import React from 'react';
import styled from 'styled-components'
import bg from '../../img/bgReg.jpg'
import Logo from "../logo/Logo";

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: url(${bg}) center/cover no-repeat;
`;
const H1 = styled.h1`
  font-size: 3rem;
  letter-spacing: 2px;
  margin-bottom: 20px;
`;
const Author = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 15px;
`;
const Name = styled.p`
  font-size: 1.2rem;
  margin-right: 10px;
  width: 200px;
  @media(min-width: 768px){
    width: 300px;
  }
`;
const Social = styled.div`
  display: flex;
  align-items: center;
`;
const A = styled.a`
  text-decoration: none;
`;
const Svg = styled.svg`
  width: 35px;
  height: 35px;
  color: #000;
  margin-right: 10px;
  cursor: pointer;
 :hover{
  color: #F9B613;
  }
`;
